@mixin underline-basic(
    $inactive-color: $color-linkblue,
    $hover-color: $color-linkblue,
    $underline-width: $border-width-thick,
    $outline: $color-linkblue
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    color: $inactive-color;
    padding-bottom: $border-width-default;
    border-bottom-color: $inactive-color;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;

    &:visited {
        color: $inactive-color;
    }

    &:focus {
        color: $inactive-color;
        border-bottom-color: $inactive-color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }

    &:hover {
        @include media('hover') {
            color: $hover-color;
            border-bottom-color: $inactive-color;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
        }
    }
}
